.catalog-product {

    &__list {
        margin-left: -15px;
        margin-right: -15px;
        display: flex;
        flex-wrap: wrap;
        margin-top: 20px;
    }

    &__item{
        padding: 0 15px;
        width: 100%;
        max-width: 33.3333%;

        &-container{
            background-color: $c_white;
            -webkit-box-shadow: 0 0 12px 0 rgba(0,0,0,0.1);
            -moz-box-shadow: 0 0 12px 0 rgba(0,0,0,0.1);
            box-shadow: 0 0 12px 0 rgba(0,0,0,0.1);
            border-radius: 5px;
        }
    }

    &__img{
        height: 240px;
        padding: 25px 25px 0;
        position: relative;

        &-link{
            display: flex;
            justify-content: center;
            align-items: center;
            height: 100%;
        }

        &-container{
            display: flex;
            justify-content: center;
            align-items: center;
            overflow: hidden;
            height: 100%;

            img{
                height: 100%;
                width: auto;
                max-width: 10000%;
            }
        }
    }

    &__label{
        position: absolute;
        display: flex;
        top: 15px;
        left: 15px;

        &-new{
            display: flex;
            justify-content: center;
            align-items: center;
            height: 25px;
            min-width: 65px;
            background-color: $c_ponos;
            border-radius: 5px;
            font-size: 13px;
            color: $c_white;
        }

        &-sale{
            display: flex;
            justify-content: center;
            align-items: center;
            height: 25px;
            min-width: 65px;
            background-color: $c_ponos;
            border-radius: 5px;
            font-size: 13px;
            color: $c_white;
        }
    }

    &__title{
        margin-top: 25px;
        padding: 0 25px;

        &-link{
            display: flex;
        }

        &-name{
            font-size: 15px;
            @include museo500();
            height: 66px;
        }
    }

    &__card{
        padding: 0 25px 25px;

        &-but{
            display: flex;
        }

        &-plus,
        &-min{
            display: flex;
            width: 26px;
            height: 26px;
            border-radius: 13px;
            border: 1px solid $c_gray_btn;
            padding: 0;
            background-color: $c_gray_btn;
            justify-content: center;
            color: $c_white;
            position: relative;
            cursor:pointer;

            &:focus{
                outline: 0;
            }

            &:before,
            &:after{
                position: absolute;
                content: "";
                display: flex;
                width: 10px;
                height: 2px;
                background-color: $c_white;
                top: 50%;
                margin-top: -1px;
            }

            &:after{
                transform: rotate(90deg);
            }

            &:hover{
                background-color: $c_white;

                &:before,
                &:after{
                    background-color: $c_gray_btn;
                }
            }
        }

        &-min{

            &:after{
                content: none;
            }
        }

        &-did{
            display: flex;
            height: 26px;
            width: 35px;
            border: 0;
            text-align: center;

            &:focus{
                outline: 0;
            }
        }

        &-calc{
            display: flex;
            width: 100%;
            max-width: 50%;
            align-items: center;
        }

        &-buy{
            width: 100%;
            max-width: 50%;
        }

        &-boot{
            width: 100%;
            height: 36px;
            justify-content: center;
            align-items: center;
            border: 1px solid $c_func_color;
            border-radius: 18px;
            cursor: pointer;
            background-color: $c_func_color;
            color: $c_white;
            font-size: 14px;
            padding: 0;
            @include museo500();

            &:focus{
                outline: 0;
            }

            &:hover{
                background-color: $c_white;
                color: $c_func_color;
            }
        }

        &-but{
            display: flex;
            width: 100%;
        }

        &-price{
           @include museo700();
            font-size: 18px;

        }
    }
}